import { useRef, useEffect, useState } from "react";
// import { BrowserMultiFormatReader, NotFoundException } from "@zxing/library";
import { BrowserMultiFormatReader } from "src/lib/esm/browser/BrowserMultiFormatReader";
import { NotFoundException } from "src/lib/esm";
import Box from "@mui/material/Box";
import AppBar from "@mui/material/AppBar";
import CssBaseline from "@mui/material/CssBaseline";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import { Button, Grid, Modal, ThemeProvider, createTheme } from "@mui/material";
import { useNavigate } from "react-router-dom";
import axios, { AxiosError } from "axios";
import { HOST_URL } from "src/configs/constans";

const theme = createTheme({
  palette: {
    primary: {
      main: "#00008b",
    },
    secondary: {
      // This is green.A700 as hex.
      main: "#87ceeb",
    },
  },
});

const style = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

export const Decoder = () => {
  const [open, setOpen] = useState<boolean>(false);
  const [openModal, setOpenModal] = useState<boolean>(false);
  const [errorMsg, setErrorMsg] = useState<string>("");

  const router = useNavigate();
  const videoRef = useRef<HTMLVideoElement | null>(null);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const codeReader = new BrowserMultiFormatReader();

  useEffect(() => {
    const mobileToken = window.localStorage.getItem("mobileToken");
    if (mobileToken === null) {
      router("/mobile");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const mobileToken = window.localStorage.getItem("mobileToken");
    if (videoRef.current) {
      codeReader.decodeFromVideoDevice(
        null,
        videoRef.current,
        (result, err) => {
          if (result) {
            // QRコードの内容が読み取られたときの処理
            codeReader.reset();
            axios.post(HOST_URL + "/api/v1/mobile-decoder", {
              otpInput: result.getText(),
              mobileToken: mobileToken,
            })
              .then((res: any) => {
                if (res.status === 200) {
                  setOpen(false);
                  setOpenModal(true);
                  codeReader.reset();
                } else {
                  setOpen(true);
                  // @ts-ignore
                  setErrorMsg(e.response?.data.message);
                  setOpenModal(false);
                }
              })
              .catch((e: AxiosError) => {
                setOpen(true);
                // @ts-ignore
                setErrorMsg(e.response?.data.message);
                setOpenModal(false);
              });
            if (err && !(err instanceof NotFoundException)) {
              // QRコードが見つからない場合以外のエラーが発生したときの処理
              console.error(err);
            }
          }
        }
      );
    }
    return () => {
      codeReader.reset();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [codeReader]);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AppBar position="relative" color={"secondary"}>
        <Toolbar sx={{ display: "flex", justifyContent: "space-between" }}>
          <Typography variant="h6" color="inherit" noWrap>
            {"Decoder"}
          </Typography>
          <Box
            component="img"
            sx={{
              height: 59,
              width: 200,
            }}
            alt="KOBE UNIVERSITY"
            src="https://www.kobe-u.ac.jp/images/common/logo_basic.svg"
          />
        </Toolbar>
      </AppBar>
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "100vh",
          bgcolor: "background.paper",
          pt: 8,
          pb: 6,
        }}
      >
        <video ref={videoRef} style={{ width: "50%", height: "50%" }} />
      </Box>
      <Modal open={open}>
        <Box sx={style}>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            {errorMsg}
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            再度QRコードを読み取ってください．
          </Typography>
          <Grid container>
            <Grid item xs={12} sx={{ mt: 2 }}>
              <Button
                variant="contained"
                fullWidth
                onClick={() => {
                  window.location.reload();
                }}
              >
                戻る
              </Button>
            </Grid>
          </Grid>
        </Box>
      </Modal>
      <Modal open={openModal}>
        <Box sx={style}>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            認証に成功しました．
          </Typography>
          <Grid container>
            <Grid item xs={12} sx={{ mt: 2 }}>
              <Button
                variant="contained"
                fullWidth
                onClick={() => {
                  window.location.reload();
                }}
              >
                戻る
              </Button>
            </Grid>
          </Grid>
        </Box>
      </Modal>
    </ThemeProvider>
  );
};
