import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

export const Home = () => {
  const router = useNavigate();

  useEffect(() => {
    const mobileToken = window.localStorage.getItem("mobileToken");
    if (mobileToken !== null) {
      router("/mobile/decoder");
    }
    else{
      router("/mobile/signin");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return null;
};
